.navbar {
  display: flex;
  justify-content: space-evenly;
  background-color: $primary;
  padding: 2rem;
  &__navlink {
    border: none;
    box-shadow: 2px 2px 8px $dark;
    font-size: 3rem;
    line-height: 150%;
    background-color: $secondary;
    border-radius: $br;
    padding: 1rem;
    transition: all ease-in-out 0.25s;

    &:hover {
      transition: all ease-in-out 0.25s;
      box-shadow: $smooth-shadow;
      cursor: pointer;
      transform: scale(1.05,1.05);
    }
  }
}